.footer__copyright-list {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 14px 50px;
  box-sizing: border-box;
  width: 100%;
  max-width: 1510px;
  list-style: none; }

@media (max-width: 1023px) {
  .footer__copyright-list {
    padding: 21px 25px; } }

@media (max-width: 767px) {
  .footer__copyright-list {
    flex-direction: column;
    align-items: center;
    padding: 28px 15px; } }

.footer__copyright-item {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1B2D37;
  opacity: 0.5;
  margin: 0;
  padding: 0; }

@media (max-width: 767px) {
  .footer__copyright-item {
    margin-bottom: 21px; } }

.footer__copyright-link {
  color: #1B2D37;
  text-decoration: none;
  margin: 0;
  padding: 0; }
  .footer__copyright-link:hover, .footer__copyright-link:focus {
    color: #00B2FF; }

.footer__copyright-item_type_company {
  margin-right: 142px; }

@media (max-width: 1023px) {
  .footer__copyright-item_type_company {
    display: none; } }

.footer__copyright-item_type_developer {
  margin-left: auto; }

@media (max-width: 767px) {
  .footer__copyright-item_type_developer {
    margin: 0; } }

.footer__copyright-item_type_rights {
  margin-right: 71px; }

@media (max-width: 1023px) {
  .footer__copyright-item_type_rights {
    display: none; } }

@media (max-width: 767px) {
  .footer__item_closed {
    display: none; } }
